/* Footer Styles */
.footer {
   background-color: #282c34;
   color: white;
   padding: 40px 20px;
   text-align: center;
 }
 
 .footer-container {
   max-width: 1200px;
   margin: 0 auto;
   display: flex;
   flex-direction: column;
   gap: 40px;
 }
 
 .footer-info h2 {
   font-size: 2rem;
   margin-bottom: 10px;
 }
 
 .footer-info p {
   font-size: 1.1rem;
   margin-bottom: 30px;
 }
 
 .footer-contact {
   display: flex;
   justify-content: space-around;
   gap: 20px;
   flex-wrap: wrap;
 }
 
 .footer-contact div {
   flex: 1;
   min-width: 250px;
   margin-bottom: 20px;
 }
 
 .footer-contact h3 {
   font-size: 1.5rem;
   margin-bottom: 10px;
 }
 
 .footer-contact p {
   font-size: 1.1rem;
   margin: 0;
 }
 
 .footer-social {
   margin-top: 30px;
 }
 
 .footer-social h3 {
   font-size: 1.5rem;
   margin-bottom: 10px;
 }
 
 .social-icons i {
   font-size: 2rem;
   margin: 0 15px;
   cursor: pointer;
   transition: color 0.3s ease;
 }
 
 .social-icons i:hover {
   color: #ffcc00; /* Change the hover color to make it vibrant */
 }
 
 /* Responsive Design */
 @media (max-width: 768px) {
   .footer-container {
     flex-direction: column;
     text-align: center;
   }
 
   .footer-contact {
     flex-direction: column;
     align-items: center;
   }
 }
 